import http from './http.js'

// 

//
export const getTab = (params) => {
	// 
	//httpurl,method
	return http({
		url: '/home/config',
		method: 'get'
	})
}
export const getRem = (params) => {
	// 
	//httpurl,method
	return http({
		url: '/home/recommend',
		method: 'get'
	})
}

//
export const getVideo = (articleId) => {
	return http({
		url: `article/${articleId}`,
		method: 'get'
	})
}
//
export const getVideoUrl = (vid) => {
	return http({
		url: `media/${vid}`,
		method: 'get'
	})
}
//
export const getVideoSame=(articleId)=>{
	return http({
		url:`article/${articleId}/next`,
		method: 'get'
	})	
}
//
export const getComment=(articleId,page)=>{
	return http({
		url:`comments?resource_id=${articleId}&type=article&page=${page}`,
		method: 'get'
	})	
}

//
export const getMy=(userId)=>{
	return http({
		url:`user/10020974`,
		method: 'get'
	})	
}
//
export const getAlbum=(userId)=>{
	return http({
		url:`user/10020974/collections`,
		method: 'get'
	})	
}
//
export const getNotes=()=>{
	return http({
		url:`notes`,
		method: 'get'
	})	
}

//
export const getCategoryContent = () => {
    // 
    return http({
        //url: 'article'+articled,
		url:`/page/discovery`,
        method: 'get'
    })
}
//
export const getSearchSub = (value) => {
	// http url  method  
	return http({
	
		url: `/search?kw=${value}`,
		method: 'get'
	})
}