// import Vue from 'vue'

// //
// import { Toast } from 'vant'

// //
// Vue.use(Toast)

import { showToast } from 'vant';


//
class ValidForm {

  constructor() {}

  //
  valid(o) {
    //o:  object
    // 
    // 

    for (let key in o) {
      if (!o[key].reg.test(o[key].value)) {
        

        //
        showToast(o[key].errorMsg);
        //
        return false;
      }
    }

    //
    return true;
  }

}

//
export const validForm = new ValidForm();