<template>
	<div class="login">
		<div class="top">
			<div class="left">
				<img src="https://oss-xpc0.xpccdn.com/passport/avatar/10755768/2022/7/62c52a7a7531b.jpg" alt="" />
				<span>New Video</span>
			</div>

			<div class="home-text" @click="toRecommend"></div>
		</div>

		<div class="welcome"></div>

		<div class="english">Please login to your accounts</div>

		<div class="form">
			<van-form @submit="onSubmit">
				<van-cell-group inset>
					<van-field v-model="data.login.phone" name="" label="" placeholder=""
						:rules="[{ required: true, message: '' }]" />
					<van-field v-model="data.login.password" type="password" name="" label="" placeholder=""
						:rules="[{ required: true, message: '' }]" />
				</van-cell-group>

			</van-form>

		</div>

		<div class="forget-password" @click="toFogotPwd" ></div>

		<div class="btn-login" @click="loggin"></div>

		<div class="btn-register" @click="changeShow"></div>

		<!--  -->
		<div class="register-box">
			<!--  -->
			<van-popup v-model:show="showBottom" position="bottom" closeable close-icon="close"
				:style="{ height: '50%' }">
				<div class="content">
					<div class="register-tilte"></div>
					<van-form @submit="onSubmit">
						<van-cell-group inset>
							<!--  -->
							<van-field v-model="data.register.phone" name="" label="" placeholder="" />
							<van-field v-model="data.register.password" type="password" name="" label=""
								placeholder="" />
							<van-field v-model="data.register.nickName" name="" label="" placeholder="" />
						</van-cell-group>
					</van-form>

					<div class="btn-login" @click="registerData"></div>
				</div>
			</van-popup>
		</div>
	</div>
</template>

<script setup>
	import '@/assets/less/login.less'
	import {
		reactive,
		ref
	} from 'vue'

	let Router = useRouter();
	let Route = useRoute();
	let toRecommend =()=>{
		Router.push({
			path: "/recommend"
		})
	};
	let toFogotPwd =()=>{
		Router.push({
			path: "/forgotPwd"
		})
	}
	let data = reactive({
		register: {
			phone: "",
			password: "",
			nickName: ""
		},
		login: {
			phone: "",
			password: ""
			
		}
	})

	let showBottom = ref(false);
	//

	let changeShow = () => {
		showBottom.value = true;
	}
	let loggin =()=>{
		axios({
			url: "http://www.kangliuyong.com:10002/login",
			method: "post",
			headers: {
			        'content-type': 'application/x-www-form-urlencoded;charset=utf-8' // 
			      },
			data: {
				appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
				// nickName: '',
				// password: 'dwq231',
				// phone: 16567789061
				// nickName: data.register.nickName,
				password: data.login.password,
				phone: data.login.phone
			}
		}).then(res => {
			console.log('res', res)
			if(res.data.code==200){
				//
				showSuccessToast('success')
				Router.push({
					path: "/recommend"
				})
			}else{
				showFailToast('faile')
			}
		}).catch(err => {
			console.log('err', err)
		})
	}
	import {
		validForm
	} from '@/common/validForm.js'
	import axios from 'axios'
	import {showSuccessToast,showFailToast,showLoadingToast,Toast} from 'vant';
	import {
		useRoute,
		useRouter
	} from "vue-router"
	
	
	let registerData = () => {
		console.log("data", data.register)
		//       
		let o = {
			phone: {
				value: data.register.phone,
				errorMsg: '',
				reg: /^1[3-9]\d{9}$/
			},
			password: {
				value: data.register.password,
				errorMsg: '(6-16)',
				reg: /^[A-Za-z]\w{5,15}$/
			},
			nickName: {
				value: data.register.nickName,
				errorMsg: '(1-10)',
				reg: /^[\w\u4e00-\u9fa5]{1,10}$/
			},
		};

		let isPass = validForm.valid(o);
		//true false
		console.log("isPass", isPass)

		if (!isPass) {
			return //
		}
		showLoadingToast({
			message:'...',
			forbidClick:true,
		});
		//
		axios({
			url: "http://www.kangliuyong.com:10002/register",
			method: "post",
			headers: {
			        'content-type': 'application/x-www-form-urlencoded;charset=utf-8' // 
			      },
			data: {
				appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
				// nickName: '',
				// password: 'dwq231',
				// phone: 16567789061
				nickName: data.register.nickName,
				password: data.register.password,
				phone: data.register.phone
			}
		}).then(res => {
			console.log('res', res)
			if(res.data.code==100){
				//
				showSuccessToast(res.data.msg)
			}else{
				showFailToast(res.data.msg)
			}
		}).catch(err => {
			console.log('err', err)
		})
	}
</script>

<style lang="less">
</style>